<template>
  <div class="register">
    <el-container>
      <el-header style="height: 70px;">
        <ComHeader/>
      </el-header>
      <el-main class="company-info" v-show="!show">
        <navTitle></navTitle>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :xs="24" :sm="20" :md="17" :lg="14" :xl="10">
            <el-form :model="ruleForm1" :rules="rules1" ref="ruleForm1" label-position="left" label-width="150px" class="demo-ruleForm" status-icon>
              <el-form-item label=" : " prop="name">
                <el-col :span="18">
                  <el-input v-model="ruleForm1.name"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="region">
                <el-col :span="18">
                  <span style="color:#5b5b5b"></span>
                </el-col>
              </el-form-item>
              <el-form-item label=" :" prop="PlaceID">
                <el-col :span="18">
                  <el-cascader
                    :options="options" style="width:100%"
                    v-model="ruleForm1.PlaceID"
                    :props="defaultProps"
                    :clearable='true'
                    :change-on-select='true'></el-cascader>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="address">
                <el-col :span="18">
                  <el-input v-model="ruleForm1.address"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="fileSuccess">
                <el-col :span="18" class="file-onload" v-bind:class="{'warning':warning}">
                  <el-upload :action="uploadUrl"
                    list-type="picture-card"
                    :auto-upload="true"
                    :data="{'module':'BUS_LICENCE'}"
                    :on-success="uploadSuccess"
                    :limit="1"
                    :on-preview="handlePictureCardPreview"
                    :file-list="fileList"
                    :on-remove="handleRemove"
                    :on-change="data_img"
                    :before-remove="beforeRemove"
                    v-model="ruleForm1.fileSuccess">
                    <i class="el-icon-plus"></i>
                  </el-upload>
                  <el-dialog :visible.sync="dialogVisible" size="tiny">
                    <img width="100%" :src="dialogImageUrl" alt>
                  </el-dialog>
                </el-col>
              </el-form-item>
              <el-form-item>
                <el-col :span="18">
                  <el-button type="primary" round @click="submitForm1('ruleForm1')" class="next-btn"></el-button>
                </el-col>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-main>
      <el-main class="person-info" v-show="show">
        <navTitle></navTitle>
        <el-row type="flex" class="row-bg" justify="center">
          <el-col :xs="24" :sm="20" :md="17" :lg="14" :xl="18">
            <el-form :model="ruleForm2" :rules="rules2" ref="ruleForm2" label-width="230px" class="demo-ruleForm" status-icon>
              <el-form-item label="  : " required>
                <el-col :span="9">
                  <el-form-item prop="username">
                    <el-input v-model="ruleForm2.username"></el-input>
                  </el-form-item>
                </el-col>
                <el-col class="line" :span="1"><span>&nbsp;</span></el-col>
                <el-col :span="5">
                  <el-form-item prop="sex">
                    <el-select v-model="ruleForm2.sex" placeholder="">
                      <el-option label="" value="1"></el-option>
                      <el-option label="" value="0"></el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="birthday" >
                <el-col :span="9">
                  <el-date-picker type="date" placeholder="" v-model="ruleForm2.birthday" style="width: 100%;"></el-date-picker>
                </el-col>
              </el-form-item>
              <el-form-item label="  : " prop="position">
                <el-col :span="9">
                  <el-input v-model="ruleForm2.position"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " >
                <span class="phone">{{ ruleForm2.mobile }}</span>
              </el-form-item>
              <el-form-item label=" :" prop="pwd" required>
                <el-col :span="15">
                  <el-input type="password" v-model="ruleForm2.pwd" auto-complete="off" placeholder="8-16"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="checkPass" required>
                <el-col :span="15">
                  <el-input type="password" v-model="ruleForm2.checkPass" auto-complete="off" placeholder=""></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" : " prop="phone">
                <el-col :span="15">
                  <el-input v-model="ruleForm2.phone"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item label=" " prop="email" :rules="[
                    { required: false, message: '', trigger: 'blur' },
                    { type: 'email', message: '', trigger: 'blur,change' }
                  ]">
                <el-col :span="15">
                  <el-input v-model="ruleForm2.email"></el-input>
                </el-col>
              </el-form-item>
              <el-form-item prop="checked">
                <el-checkbox-group v-model="ruleForm2.checked">
                  <el-checkbox> </el-checkbox><span style="font-size:12px;"> <span class="phone"> </span><span class="blur-color"></span></span>
                </el-checkbox-group>
              </el-form-item>
              <el-form-item>
                <el-col :span="15">
                  <el-button type="primary" @click="submitForm2('ruleForm2')" class="next-btn"></el-button>
                  <!-- <div @click="submitForm('ruleForm')" class="next-btn" v-bind:class="{'disabled':ruleForm.checked==false}"></div> -->
                </el-col>
              </el-form-item>
            </el-form>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </div>

</template>

<script>
  import ComHeader from './header'
  import navTitle from '@/components/navTitle'
  import request from '@/utils/request'
  import data from '@/utils/pcas-code.json'
  import { clearCity } from '@/utils/clearCity'
  import { getSSession } from '@/utils/session'
  import { baseURL } from '@/utils/api'
  export default {
    data () {
      let isok = (rule, value, callback) => {
        this.iscom = false
      }
      let checkComName = (rule, value, callback) => {
        if (!value) {
          callback(new Error(''))
        } else {
          if (value < 3) {
            callback(new Error('3'))
          } else if (this.iscom === false) {
            let formData = new FormData()
            formData.append('name', this.ruleForm1.name)
            request.checkComName({
              name: this.ruleForm1.name
            }).then(({ data }) => {
              if (data.success) {
                this.iscom = true
                callback()
              } else {
                callback(new Error(''))
              }
            })
          }
        }
      }
      let validatePass = (rule, value, callback) => {
       let reg = /^\w{8,16}$/
        if (!reg.test(value)) {
          callback(new Error(''))
        } else {
          if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass')
          }
          callback()
        }
      }
      let validatePass2 = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''))
        } else if (value !== this.ruleForm2.pwd) {
          callback(new Error(''))
        } else {
          callback()
        }
      }
      let validatePass3 = (rule, value, callback) => {
       let reg = /^0\d{2}-\d{7,8}$/;
       if(reg.test(value)){
        callback()
       }else{
        callback(new Error(''))
       }
      }
      return {
        show: false,
        iscom: false,
        dialogImageUrl: '',
        dialogVisible: false,
        'auto-upload': false,
        warning: false,
        fileList: [],
        options: [],
        defaultProps: {
          children: 'childs',
          label: 'name',
          value: 'code'
        },
        ruleForm1: {
          name: '',
          PlaceID: [],
          address: '',
          fileSuccess: ''
        },
        rules1: {
          name: [
            { required: true, validator: checkComName, trigger: 'blur' },
            { validator: isok, trigger: 'change' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' },
            { min: 5, max: 25, message: '5-25', trigger: 'blur' }
          ],
          PlaceID: [
            { type: 'array', required: true, message: '', trigger: 'change' }
          ],
          fileSuccess: [
            { required: true, message: '', trigger: 'change' }
          ]
        },
        ruleForm2: {
          username: '',
          sex: '',
          birthday: '',
          position: '',
          mobile: getSSession('hymobile'),
          pwd: '',
          checkPass: '',
          phone: '',
          email: '',
          checked: []
        },
        rules2: {
          username: [
            { required: true, message: '', trigger: 'blur' },
            { min: 2, max: 10, message: '2', trigger: 'blur' }
          ],
          sex: [
            { required: true, message: '', trigger: 'change' }
          ],
          pwd: [
            { validator: validatePass, trigger: 'blur' }
          ],
          checkPass: [
            { validator: validatePass2, trigger: 'blur' }
          ],
          checked: [
            { required: true,   message: '', trigger: 'change' }
          ]
        }
      }
    },
    components: {
      ComHeader,
      navTitle
    },
    mounted () {
      this.options = clearCity(data)
    },
    computed: {
      uploadUrl () {
        return `${baseURL}oss/upload`
      }
    },
    methods: {
      submitForm1 (formName) {
        if (!this.ruleForm1.name) {
          this.$message.error('')
          return
        }
        if (!this.ruleForm1.address) {
          this.$message.error('')
          return
        }
        if (!this.ruleForm1.fileSuccess) {
          this.warning = true
          this.$message.error('')
          return
        }
        if (this.iscom === true) {
          this.show = true
        }
      },
      uploadImg (request) {},
      handleRemove (file, fileList) {
        this.warning = true
        this.ruleForm1.fileSuccess = ''
      },
      handlePictureCardPreview (file) {
        this.dialogImageUrl = file.url
        this.dialogVisible = true
      },
      data_img (file, fileList) {
        if (file.url) {
          this.ruleForm1.fileSuccess = file.url
          this.warning = false
        }
      },
      beforeRemove (file, fileList) {
        return this.$confirm(`${file.name}`)
      },
      uploadSuccess (response, file, fileList) {
        this.fileSuccess = response.data
      },
      submitForm2 (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let formData = this.ruleForm1
            request.registerComInfo({
              name: formData.name,
              type: '1',
              proid: formData.PlaceID[0],
              cityid: formData.PlaceID[1],
              areaid: formData.PlaceID[2],
              address: formData.address,
              busLicensePhoto: String(this.fileSuccess),
              username: this.ruleForm2.username,
              pwd: this.ruleForm2.pwd,
              birthday: this.ruleForm2.birthday,
              email: this.ruleForm2.email,
              mobile: this.ruleForm2.mobile,
              phone: this.ruleForm2.phone,
              sex: this.ruleForm2.sex,
              position: this.ruleForm2.position
            }).then(({ data }) => {
              if (data.success) {
                this.$message({ message: '', type: 'success' })
                this.$nextTick(() => {
                  this.$router.push({ path: '/login' })
                })
              } else {
                this.$message.error(data.message)
              }
            })
          } else {
            return false
          }
        })
      }
    }
  }
</script>

<style>

  html,body{
    background: #ecf0f9;
  }
  .register .el-container{
    width: 1200px;
    margin: 0 auto;
    min-height: 920px;
  }
  .register .el-main{
    width: 100%;
    padding: 46px 0;
    background: #ffffff;
    box-shadow: 0px 0px 5px #d8dbe2;
  }
  .register .next-btn{
    width: 100%;
    height: 50px;
    border-radius: 40px;
    color: #ffffff;
    text-align: center;
    font-size: 20px;
    cursor: pointer;
    background: #459FFC;
    background: linear-gradient(90deg, rgba(58,147,281,1), rgba(60,226,200,1));
    margin: 20px 0;
    border: none;
  }
  .register .next-btn:hover{
    background: #A1CFFE;
    background: linear-gradient(90deg, rgba(120,200,240,1), rgba(120,236,224,1));
  }
  .company-info .file-onload{
    margin-top: 10px;
    border: dashed 2px #d7d7d7;
    padding: 15px;
    border-radius: 4px;
  }
  .company-info .el-upload-list--picture-card .el-upload-list__item{
    margin-bottom: 0;
  }
  .company-info .el-col-18 .el-form-item__content{
    line-height: 0;
  }
  .company-info .warning{
    border: dashed 2px #ff0000;
  }
  .company-info .el-form-item__label{
    color: #474646;
    font-size: 16px;
  }
  .person-info .phone{
    color: #5b5b5b;
  }
  .person-info .blur-color{
    color: #397bf1;
  }
</style>



